.menu-desktop 
{
   display: none;
}

.active
{
   transform: translate(-600px);
}

.menu-list
{
   background-image: $background_menu;
   box-shadow: 2px 0px 5px 2px #b7abab;
   height: 100vh;
   left: 0;
   position: absolute;
   top: 0;
   transition: all .5s ease-in;
   width: 400px;
   z-index: 1;

   .list
   {
      list-style: none;
      padding: 100px 0px 20px 0px;

      &__item a
      {
         color: $black;
         cursor: pointer;
         display: block;
         font-size: 22rem;
         padding: 10px 40px;
         text-decoration: none;
      }

      &__item:hover
      {
         background-color: rgb(228,228,228);
         color: rgb(42,42,42);
      }
   }
}

.menu-mobile
{
   background-image: $background_icon;
   border-radius: 5px;
   cursor: pointer;
   height: 40px;
   left: 30px;
   margin: 0;
   position: absolute;
   transition: 1s ease-in;
   top: 20px;
   width: 40px;
   z-index: 1;

   .icon-burger
   {
      height: 30px;
      margin: 5px;
      position: relative;
      width: 30px;

      &__line-1 
      {
         transform: translate(-50%, -13px);
      }

      &__line-3
      {
         transform: translate(-50%, 10px);
      }
   }

   span 
   {
      background: $white;
      border-radius: 5px;
      display: block;
      height: 3px;
      left: 50%;
      position: absolute;
      transform: translate(-50%, -50%);
      transition: transform 0.5s, width 0.5s;
      top: 50%;
      width: 100%;
   }
}

.openmenu .icon-burger__line-1 
{
   transform: translate(-50%, -50%) rotate(-45deg);
}

.openmenu .icon-burger__line-3 
{
   transform: translate(-50%, -50%) rotate(45deg);
}

.openmenu .icon-burger__line-2 
{
   width: 0;
}

@media screen and (min-width: 1024px)
{
   .menu-mobile 
   {
      display: none;
   } 

   .menu-desktop
   {
      display: flex;
      height: 37px;
      margin: 0;
      margin-left: 173px;
      position: relative;
      top: 44px;

      &__item
      {
         box-sizing: border-box;
         list-style: none;
         position: relative;
         margin-right: 40px;
         width: 100px;

         a 
         {
            color: $white;
            font-size: 16rem;
            font-weight: normal;
            letter-spacing: 1.5px;
            text-decoration: none;
         }

         a::before 
         {
            background-color: white;
            bottom: 8px;
            content: "";
            height: 2px;
            left: 0;
            margin-left: 0;
            position: absolute;
            transition: all .5s ease;
            width: 0;
         }

         a:hover::before
         {
            width: 20px;
         }

         a:hover 
         {
            font-weight: bold;
         }
      }

      &__item:first-child
      {
         width: 60px;
      }

      &__item:nth-child(2)
      {
         width: 180px;
      }
   }   
}